<template lang="pug">

    v-card 
        v-card-title 
            h2 数据表

        v-card-text(class="pa-5")
            v-data-table(
                :headers="headers"
                :items="tabledata"
                :options.sync="options"
                no-data-text="暂无数据"
                dense
                :server-items-length="total_rows"
                :footer-props="{'items-per-page-options': items_per_page_options,'show-first-last-page':true}"
            )
                template(v-slot:top)
                    v-toolbar(flat class="mb-8")
                        v-row
                            v-col(cols="12" md="6" lg="4")
                                v-btn(:disabled="!load_data" class="mb-2 text-capitalize" color="primary" @click="load_csv_data" )
                                    v-icon(class="mr-2" ) mdi-reload
                                    span 加载数据
                        
                            v-col(cols="12" md="6" lg="4")
                                v-btn(:disabled="!check_can_upload" class="mb-2 text-capitalize" color="primary" @click="check_can_upload_fun" )
                                    v-icon(class="mr-2") mdi-cloud-download
                                    span 校验数据

                            v-col(cols="12" md="6" lg="4")
                                v-btn(:disabled="!upload" class="mb-2 text-capitalize" color="primary" @click="upload2db" )
                                    v-icon(class="mr-2") mdi-cloud-upload
                                    span 上传数据

</template>

<script>
// 扩展表格显示
// 2020/5/25 by oldkingnearby@gmail.com
import { sync,get} from 'vuex-pathify'
import * as api from "@/apis/wswebapi"
export default {
    data:()=>({
        height:400,
        expanded: [],
        singleExpand:true,//只允许单行扩展
        // 分页数据管理
        options:{},
        // 表内数据
        tabledata:[],
        // 总行数
        total_rows:10,
        load_data:false,
        check_can_upload:false,
        upload:false,
        batchsize:3000,
    }),
    computed: {
        items_per_page_options:sync('db/items_per_page_options'),
        // 显示的数据库配置
        collection_config:sync('db/collection_config'),
        // 表头
        headers:get('db/headers'),

        filepath:get('db/uploaded_file_path'),

        file_hash:get('db/file_hash'),

        collection:sync('db/collection'),
    },
    methods:{
        
        // 加载csv数据
        load_csv_data(){
            console.log("load_csv_data",this.file_hash,this.filepath);
            let filehash=this.file_hash;
            if (!filehash){
                return
            }
            let path=this.filepath[filehash];
            if (!path){
                console.log("文件未上传到服务器",filehash)
                return
            }
            if (!this.collection){
                console.log("未选择数据库")
                return
            }
            console.log("LocalReadColCsvData")
            api.LocalReadColCsvData({hash:filehash,collection:this.collection,path:path}).then(res=>{
                if (res.code==200){
                    console.log("LocalReadColCsvData res",res)
                    this.check_can_upload=true;
                    this.update_csv_page_data()
                }else{
                    this.$store.dispatch("msglog/push_msg",{message:res.msg+res.error,type:"error"});
                    this.check_can_upload=false;
                    this.$store.dispatch("db/update_csv_header_map",res.headermap);
                    this.tabledata=[];
                }
            })
        },
        // 刷新表格内容
        update_csv_page_data(){
            if (!this.file_hash){
                return
            }
            let page=this.options.page;
            let pagesize=this.options.itemsPerPage;
            api.LocalGetCsvPageData({hash:this.file_hash,page:page,pagesize:pagesize}).then(res=>{
                if (res.code==200){
                    console.log("更新分页成功",res);
                    this.tabledata=res.data;
                    this.total_rows=res.total;
                    let titleMap={};
                    for (var k in res.data[0]){
                        titleMap[k]=true;
                    }
                    this.$store.dispatch("db/update_csv_header_map",titleMap);
                }else{
                    console.log("更新分页失败",res);
                    this.$store.dispatch("msglog/push_msg",{message:res.msg,type:"error"})
                }
            })
        },
        // 验证是否可以上传
        check_can_upload_fun(){
            if (!this.file_hash){
                return
            }
            if (!this.collection){
                return
            }
            api.LocalCheckCsvDataCanUpload({hash:this.file_hash,collection:this.collection}).then(res=>{
                if (res.code ==200){
                    this.upload=true;
                    this.$store.dispatch("msglog/push_msg",{message:res.msg,type:"success"})
                }else{
                    console.log("无法上传到数据库",res);
                    this.$store.dispatch("msglog/push_msg",{message:res.msg+res.error,type:"error"})
                }
            })
        },
        // 开始上传到数据库
        upload2db(){
            if (!this.file_hash){
                return
            }
            if (!this.collection){
                return
            }
            api.LocalUploadCsvData2db({hash:this.file_hash,collection:this.collection,batchsize:this.batchsize}).then(res=>{
                if (res.code ==200){
                    this.$store.dispatch("msglog/push_msg",{message:res.msg,type:"success"})
                    this.upload=false;
                }else{
                    console.log("无法上传到数据库",res);
                    this.$store.dispatch("msglog/push_msg",{message:res.msg+res.error,type:"error"})
                }
            })
        }

    },
    watch:{
        options:{
            handler(){
                console.log("页面刷新",this.options)
                this.update_csv_page_data()
                // this.$store.dispatch("db/update_col_page_data",this.options);
            },
            deep:true,
        },
        file_hash(){
            console.log("文件hash变化",this.file_hash)
            this.load_data=true;
        },
        collection(){
            this.check_can_upload=false;
        }
    }

}
</script>